import React, { useContext, useEffect, useState } from "react";
import { Button, Input } from "antd";
import { TodoContext } from "../context/TodoContextProvider";
import { setTodos, updateTodo, deleteTodo } from "../context/todo.actions";
import { firebaseApi } from "../services/firebaseApi";

const TodoTask = (props) => {
  const [inputValue, setInputValue] = useState(props.description);
  const [isReadMode, setIsReadMode] = useState(true);

  return (
    <div className="todo-task">
      {isReadMode ? (
        <div className="todo-task__name" data-cy="todo-task__name">
          {inputValue}
        </div>
      ) : (
        <Input
          value={inputValue}
          onChange={({ target: { value } }) => setInputValue(value)}
          placeholder="Add a TODO"
          size="large"
          className="todo-input__input"
          data-cy="todo-input__input"
        />
      )}
      <Button
        type="primary"
        shape="round"
        className="todo-task__button"
        data-cy="todo-task__button-update"
        onClick={() => {
          setIsReadMode(!isReadMode);
          props.update(props.id, inputValue);
        }}
      >
        {isReadMode ? 'Update' : 'Save'}
      </Button>
      <Button
        type="primary"
        shape="round"
        className="todo-task__button"
        data-cy="todo-task__button-delete"
        onClick={() => props.delete(props.id)}
        disabled={!isReadMode}
      >
        Delete
      </Button>
    </div>
  );
};

export const TodoList = () => {
  const { state, dispatch } = useContext(TodoContext);

  useEffect(() => {
    const fetchTodos = async () => {
      const todos = await firebaseApi.fetchTodos();
      dispatch(setTodos(todos));
    }

    fetchTodos();
  }, [dispatch]);

  const handleUpdateTodo = async (id, description) => {
    try {
      await firebaseApi.updateTodo(id, description);
      dispatch(updateTodo(id, description));
    } catch(err) {
      alert(err);
    }
  };

  const handleDeleteTodo = async (id) => {
    try {
      await firebaseApi.deleteTodo(id);
      dispatch(deleteTodo(id));
    } catch(err) {
      alert(err);
    }
  };

  return (
    <div className="todo-list" data-cy="todo-list">
      {Object.entries(state.todos).map(([id, todo]) => (
        <TodoTask
          key={id}
          description={todo.description}
          update={handleUpdateTodo}
          delete={handleDeleteTodo}
          id={id}
        />
      ))}
    </div>
  );
};
